import React, { Component } from "react";
import { Button, Container, Row, Col, Card, Alert } from "react-bootstrap";
import { Paper } from "@material-ui/core";
import axios from "axios";
import "./style.css";
import Header from "./Header";
import Footer from "./Footer";
import AddBucket from "./AddBucket";
import EditTask from "./EditTask";
import AddTask from "./AddTask";
import Settings from "./Settings";

class MainPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      //data
      buckets_array: [],
      tasks: {},
      bucketName: "",
      showBucketModal: false,

      //These are task properties.
      // name: "",
      // description: "",
      // rank: "",
      // assignees: [],
      // assignee: "",
      // bucket: "To do",
      // start_date: new Date(),
      // due_date: new Date(),
      // progress: "",
      // label_color: "#3797a4",
      // priority: "",

      //Modal Show props.
      showAddModal: false,
      editModal: {},
      showEditModal: false,
      showSettings: false,

      //dragging variables.
      draggedCard: null,
      dragOn: false,
      dragOverBucket: null,

      draggedBucket: null,

      autoSave: true,
    };
  }

  autoSaveData() {
    window.localStorage.setItem("autoSave", this.state.autoSave);
    this.state.autoSave &&
      axios
        .post(
          "https://kanban-back-end.herokuapp.com/buckets/save",
          Object.assign({}, this.state.buckets_array)
        )
        .then(
          (response) => {},
          (error) => {
            console.log(error);
            alert("Error. Can not Load Data.");
          }
        );

    this.state.autoSave &&
      axios
        .post(
          "https://kanban-back-end.herokuapp.com/tasks/save",
          this.state.tasks
        )
        .then(
          (response) => {},
          (error) => {
            console.log(error);
            alert("Error. Can not Load Data.");
          }
        );
  }

  saveData = () => {
    this.setState({ showSettings: false });
    axios
      .post(
        "https://kanban-back-end.herokuapp.com/buckets/save",
        Object.assign({}, this.state.buckets_array)
      )
      .then(
        (response) => {
          console.log(response.data);
        },
        (error) => {
          console.log(error);
          alert("Error. Can not Load Data.");
        }
      );

    axios
      .post(
        "https://kanban-back-end.herokuapp.com/tasks/save",
        this.state.tasks
      )
      .then(
        (response) => {
          console.log(response.data);
        },
        (error) => {
          console.log(error);
          alert("Error. Can not Load Data.");
        }
      );

    this.setState({ showSettings: false });
  };

  componentDidMount() {
    this.setState({
      autoSave:
        window.localStorage.getItem("autoSave") === "true" ? true : false,
    });
    axios.get("https://kanban-back-end.herokuapp.com/buckets").then(
      (response) => {
        var arr = Object.values(response.data);
        for (var i in arr) {
          arr[i] = arr[i]["name"];
        }
        this.setState({ buckets_array: arr });
      },
      (error) => {
        console.log(error);
      }
    );

    axios
      .get("https://kanban-back-end.herokuapp.com/tasks")
      .then((response) => {
        var tasks = response.data;
        for (var i in tasks) {
          tasks[i].start_date = new Date(tasks[i].start_date);
          tasks[i].due_date = new Date(tasks[i].due_date);
        }

        this.setState({ tasks: tasks });
      });

    setInterval(() => {
      this.autoSaveData();
    }, 5000);
  }

  // This function is used to add a new task
  addTask = (task) => {
    console.log(task);
    var tasks = this.state.tasks;
    var n = Object.keys(tasks).length + 1;
    var rank = 1;
    for (var i in tasks) {
      if (tasks[i].bucket === task.bucket) rank++;
    }
    task.id = n;
    task.rank = rank;
    tasks[task.id] = task;
    this.setState({
      tasks: tasks,
      showAddModal: false,
    });
  };

  editTask(task) {
    this.setState({ editModal: task });
    this.setState({
      name: task.name,
      description: task.description,
      bucket: task.bucket,
      assignees: task.assignees,
      start_date: task.start_date,
      due_date: task.due_date,
      progress: task.progress,
      label_color: task.label_color,
      priority: task.priority,
      showEditModal: true,
    });
  }

  saveEditTask = (task) => {
    var tasks = this.state.tasks;
    tasks[task.id] = task;
    this.setState({ tasks: tasks, showEditModal: false });
  };
  deleteTask = (task) => {
    var bucket = task.bucket;
    var id = task.id;
    var temp = this.state.tasks;
    for (var i in temp) {
      if (temp[i].bucket === bucket && temp[i].rank > task.rank) temp[i].rank--;
    }
    delete temp[id];
    this.setState({ tasks: temp, showEditModal: false });
  };
  //bucket
  saveBucket = (bucketName) => {
    var arr = this.state.buckets_array;
    arr.push(bucketName);
    this.setState({
      buckets_array: arr,
      showBucketModal: false,
      bucketName: "",
    });
  };

  dragStart(e, task) {
    this.setState({ draggedCard: task, dragOn: true });
  }

  dragEnd(e) {
    this.setState({ draggedCard: null, dragOn: false, dragOverBucket: null });
  }

  dragOver(e, bucket) {
    e.preventDefault();
    if (this.state.dragOverBucket !== bucket)
      this.setState({ dragOverBucket: bucket });
    // console.log(this.state.dragOverBucket);
  }

  dropCard(e, index) {
    if (this.state.draggedBucket !== null) {
      var draggedBucket = this.state.draggedBucket;
      var arr = this.state.buckets_array;
      var target = arr[index];
      var target_index = arr.indexOf(target);
      arr = arr.filter(function (ele) {
        return ele !== draggedBucket;
      });
      console.log(arr, target_index);
      arr.splice(target_index, 0, draggedBucket);
      this.setState({ buckets_array: arr });
      return;
    }
    if (this.state.draggedCard.bucket === this.state.buckets_array[index]) {
      return;
    }
    let task = this.state.draggedCard;
    let task_id = task.id;
    let toList = index;
    var tasks = this.state.tasks;
    var bucket = this.state.buckets_array[toList];

    var rank = 1;
    for (var i in tasks) {
      if (tasks[i].bucket === bucket) rank = rank + 1;
    }

    for (i in tasks) {
      if (
        tasks[i].bucket === task.bucket &&
        Number(tasks[i].rank) > Number(task.rank)
      ) {
        tasks[i].rank--;
      }
    }
    tasks[task_id].bucket = this.state.buckets_array[toList];
    tasks[task_id].rank = rank;
    this.setState({ tasks: tasks, dragOn: false, dragOverBucket: null });
  }

  dropVertical(e, task) {
    if (this.state.draggedBucket !== null) {
      return;
    }
    if (this.state.draggedCard.bucket !== task.bucket) {
      return;
    }

    var draggedRank = this.state.draggedCard.rank;
    var droppedRank = task.rank;
    var tasks = this.state.tasks;
    if (draggedRank > droppedRank) {
      for (var i in tasks) {
        if (
          tasks[i].bucket === task.bucket &&
          tasks[i].rank >= droppedRank &&
          tasks[i].rank <= draggedRank
        ) {
          tasks[i].rank++;
        }
      }
      tasks[this.state.draggedCard.id].rank = droppedRank;
      this.setState({ tasks: tasks });
    } else if (draggedRank < droppedRank) {
      for (i in tasks) {
        if (
          tasks[i].bucket === task.bucket &&
          tasks[i].rank <= droppedRank &&
          tasks[i].rank >= draggedRank
        ) {
          tasks[i].rank--;
        }
      }
      tasks[this.state.draggedCard.id].rank = droppedRank;
      this.setState({ tasks: tasks });
    }
  }

  bucketDrop(e, target) {
    if (this.state.draggedBucket === null) {
      return;
    }
    var draggedBucket = this.state.draggedBucket;
    var arr = this.state.buckets_array;
    var target_index = arr.indexOf(target);
    arr = arr.filter(function (ele) {
      return ele !== draggedBucket;
    });
    console.log(arr, target_index);
    arr.splice(target_index, 0, draggedBucket);
    this.setState({ buckets_array: arr });
  }

  // This is the render function
  render() {
    var tasks_array = [];
    var tasks = this.state.tasks;
    var buckets = this.state.buckets_array;
    var n = Object.keys(tasks).length;
    for (var i in buckets) {
      for (var j = 1; j <= n; j++) {
        for (var task in tasks) {
          if (
            Number(tasks[task].rank) === j &&
            tasks[task].bucket === buckets[i]
          )
            tasks_array.push(tasks[task]);
        }
      }
    }

    return (
      <html>
        <div className="App">
          <Header />
          {/* ----------------------------------------------Container-------------------------------------------- */}
          <Container className="board" fluid>
            {/* ---------------------Add Task Button------------------- */}
            <Row className="mt-2 mb-2">
              <Col></Col>
              <Col align="center">
                {/* <Button
                  className="mr-3"
                  onClick={() =>
                    this.setState({
                      showAddModal: true,
                    })
                  }
                  variant="info"
                >
                  <i class="fas fa-plus-circle  mr-2"></i>Add Task
                </Button> */}
                <Button
                  onClick={() => this.setState({ showBucketModal: true })}
                  variant="secondary"
                >
                  <i class="fas fa-plus-circle  mr-2"></i>Add Bucket
                </Button>
              </Col>
              <Col>
                <Button
                  variant="outline-dark"
                  className="float-right mr-4"
                  onClick={() => this.setState({ showSettings: true })}
                >
                  Settings<i class="fas fa-cog  settings-icon ml-2"></i>
                </Button>
              </Col>
            </Row>

            {/* ---------------------Task Lists------------------- */}

            <Row className="board">
              {/* ---------------------Buckets------------------- */}
              {this.state.buckets_array.map((bucket) => (
                <Col
                  id={this.state.buckets_array.indexOf(bucket)}
                  className="bucket"
                  onDragOver={(e) => this.dragOver(e, bucket)}
                  onDrop={(e) =>
                    this.dropCard(e, this.state.buckets_array.indexOf(bucket))
                  }
                >
                  <Paper elevation={3} className="paper-list">
                    <Alert
                      variant="primary"
                      style={{ padding: "5px" }}
                      draggable="true"
                      onDragStart={(e) => {
                        this.setState({ draggedBucket: bucket });
                      }}
                      onDragEnd={(e) => {
                        this.setState({ draggedBucket: null, dragOn: false });
                      }}
                      onDragOver={(e) => {
                        e.preventDefault();
                      }}
                      onDrop={(e) => this.bucketDrop(e, bucket)}
                    >
                      <Row>
                        <Col>
                          <h6
                            style={{ margin: "0" }}
                            className="float-left ml-2"
                          >
                            {bucket}
                          </h6>
                        </Col>
                        <Col>
                          <span style={{ cursor: "pointer" }}>
                            <i
                              class="fas fa-trash float-right bucket-edit"
                              onClick={() => {
                                var arr = this.state.buckets_array;

                                arr = arr.filter(function (ele) {
                                  return ele !== bucket;
                                });
                                var tasks = this.state.tasks;
                                for (var i in tasks) {
                                  if (tasks[i].bucket === bucket) {
                                    delete tasks[i];
                                  }
                                }
                                this.setState({
                                  buckets_array: arr,
                                  tasks: tasks,
                                });
                              }}
                            ></i>
                          </span>
                        </Col>
                      </Row>
                    </Alert>
                    {Object.values(tasks_array).map((task) =>
                      task.bucket === bucket ? (
                        <Card
                          style={{
                            opacity:
                              this.state.draggedCard === task &&
                              this.state.dragOn
                                ? 0.3
                                : 1,
                          }}
                          className="mt-2 task-card"
                          draggable="true"
                          onDragStart={(e) => this.dragStart(e, task)}
                          onDragEnd={(e) => this.dragEnd(e)}
                          onDrop={(e) => this.dropVertical(e, task)}
                        >
                          <Card.Body
                            style={{ padding: "3% 1% 3% 3%" }}
                            onClick={() => this.editTask(task)}
                          >
                            <Card.Text className="float-left mb-0 mt-1">
                              <svg
                                style={{ color: task.label_color }}
                                class="bi bi-bookmark-fill"
                                width="1em"
                                height="1em"
                                viewBox="0 0 16 16"
                                fill="currentColor"
                                xmlns="http://www.w3.org/2000/svg"
                              >
                                <path
                                  fill-rule="evenodd"
                                  d="M3 3a2 2 0 0 1 2-2h6a2 2 0 0 1 2 2v12l-5-3-5 3V3z"
                                />
                              </svg>{" "}
                              {task.name}
                            </Card.Text>

                            {/* <span
                              className="float-right mr-2"
                              style={{ cursor: "pointer", width: "10px" }}
                              onClick={() => {
                                this.editTask(task);
                              }}
                            >
                              <i class="fas fa-ellipsis-v edit-dots"></i>
                            </span> */}
                          </Card.Body>
                        </Card>
                      ) : null
                    )}
                    <Card
                      className="mt-2 task-card"
                      style={{
                        display:
                          this.state.dragOverBucket === bucket &&
                          this.state.draggedCard.bucket !== bucket
                            ? "block"
                            : "none",
                        opacity: 0.7,
                        backgroundColor: "#f7fff2",
                      }}
                    >
                      <Card.Body>
                        <Card.Text className="float-left mb-0 mt-1"></Card.Text>
                      </Card.Body>
                    </Card>

                    <Alert
                      className="add-task"
                      onClick={() =>
                        this.setState({
                          showAddModal: true,
                        })
                      }
                    >
                      <Row>
                        <Col align="center" className="add-task-content">
                          + Add another task
                        </Col>
                      </Row>
                    </Alert>
                  </Paper>
                </Col>
              ))}
            </Row>
          </Container>

          {/* ---------------------Pop Up Modal for Adding NEW Bucket------------------- */}
          <AddBucket
            visibility={this.state.showBucketModal}
            saveFunction={this.saveBucket}
            hideModal={() => this.setState({ showBucketModal: false })}
          />

          {/* ---------------------Pop Up Modal for EDITING TASK details------------------- */}
          <EditTask
            visibility={this.state.showEditModal}
            buckets={this.state.buckets_array}
            taskData={this.state.editModal}
            saveFunction={this.saveEditTask}
            deleteFunction={this.deleteTask}
            hideModal={() => this.setState({ showEditModal: false })}
          />

          {/* ---------------------Pop Up Modal for ADDING NEW TASK details------------------- */}

          <AddTask
            visibility={this.state.showAddModal}
            buckets={this.state.buckets_array}
            saveFunction={this.addTask}
            hideModal={() => this.setState({ showAddModal: false })}
          />

          {/* ---------------------Pop Up Modal for SETTINGS------------------- */}

          <Settings
            visibility={this.state.showSettings}
            autoSave={this.state.autoSave}
            toggleAutoSave={(checked) => this.setState({ autoSave: checked })}
            saveFunction={this.saveData}
            hideModal={() => this.setState({ showSettings: false })}
          />

          <Footer />
        </div>
      </html>
    );
  }
}

export default MainPage;
