import React, { useState, useEffect } from "react";
import {
  Modal,
  Button,
  Dropdown,
  InputGroup,
  FormControl,
  Row,
  Col,
  Table,
  DropdownButton,
} from "react-bootstrap";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

function AddTask(props) {
  const [task, setTask] = useState({
    name: "",
    description: "",
    assignees: [],
    assignee: "",
    rank: "",
    bucket: props.buckets[0],
    start_date: new Date(),
    due_date: new Date(),
    progress: "",
    label_color: "#3797a4",
    priority: "",
  });

  useEffect(() => {
    setTask({
      name: "",
      description: "",
      assignees: [],
      rank: "",
      bucket: "To do",
      start_date: new Date(),
      due_date: new Date(),
      progress: "",
      label_color: "#3797a4",
      priority: "",
    });
  }, [props.visibility]);

  return (
    <Modal
      size="lg"
      centered
      show={props.visibility}
      onHide={() => props.hideModal()}
    >
      <Modal.Header closeButton>
        <Modal.Title>Enter Task</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <InputGroup size="sm">
          <InputGroup.Prepend>
            <InputGroup.Text>Name</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            type="text"
            size="sm"
            onChange={(e) => {
              setTask({
                ...task,
                name: e.target.value,
              });
            }}
          />
        </InputGroup>
        <hr />
        <Row>
          <Col xs={2} className="text-center">
            <i class="fas fa-user-plus"></i> Assignees:
          </Col>
          <Col>
            <InputGroup className="w-50" size="sm">
              <FormControl
                placeholder="Add Assignee"
                onChange={(e) => setTask({ ...task, assignee: e.target.value })}
                value={task.assignee}
              />
              <InputGroup.Append>
                <Button
                  variant="outline-info"
                  onClick={() => {
                    var temp = task.assignees;
                    temp.push(task.assignee);
                    setTask({ ...task, assignees: temp, assignee: "" });
                  }}
                >
                  Add
                </Button>
                <Button
                  variant="outline-danger"
                  onClick={() => {
                    setTask({ ...task, assignees: [], assignee: "" });
                  }}
                >
                  Delete
                </Button>
              </InputGroup.Append>
            </InputGroup>
          </Col>
        </Row>

        <Table
          striped
          bordered
          hover
          size="sm"
          className="mt-3 w-80 text-center"
        >
          <thead>
            <tr>
              {task.assignees
                ? task.assignees.map((person) => <th>{person}</th>)
                : null}
            </tr>
          </thead>
        </Table>
        <hr />
        <Row className="mt-3">
          <Col>
            <Row>
              <Col className="text-center">Bucket:</Col>
              <Col>
                <DropdownButton
                  title={task.bucket}
                  size="sm"
                  onSelect={(eventKey) => {
                    setTask({ ...task, bucket: eventKey });
                  }}
                >
                  {props.buckets
                    ? props.buckets.map((bucket) => (
                        <Dropdown.Item eventKey={bucket}>
                          {bucket}
                        </Dropdown.Item>
                      ))
                    : null}
                </DropdownButton>
              </Col>
            </Row>
            <hr />
            <Row className="mt-3">
              <Col className="text-center">
                <span className="ml-4">Start Date:</span>
              </Col>
              <Col>
                <DatePicker
                  selected={task.start_date}
                  onChange={(date) => {
                    setTask({ ...task, start_date: date });
                  }}
                />
              </Col>
            </Row>
          </Col>
          <Col>
            <Row>
              <Col className="text-center">Progress:</Col>
              <Col>
                <DropdownButton
                  title={task.progress === "" ? "select..." : task.progress}
                  size="sm"
                  onSelect={(eventKey) => {
                    setTask({ ...task, progress: eventKey });
                  }}
                >
                  <Dropdown.Item eventKey="Not Started">
                    Not Started
                  </Dropdown.Item>
                  <Dropdown.Item eventKey="In Progress">
                    In Progress
                  </Dropdown.Item>
                  <Dropdown.Item eventKey="Completed">Completed</Dropdown.Item>
                </DropdownButton>
              </Col>
            </Row>
            <hr />
            <Row className="mt-3">
              <Col className="text-center">
                <span className="ml-5">Due Date:</span>
              </Col>
              <Col>
                <DatePicker
                  selected={task.due_date}
                  onChange={(date) => {
                    setTask({ ...task, due_date: date });
                  }}
                />
              </Col>
            </Row>
          </Col>
        </Row>
        <hr />
        <InputGroup className="mt-3">
          <InputGroup.Prepend>
            <InputGroup.Text>Description :</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            as="textarea"
            rows="2"
            onChange={(e) => {
              setTask({ ...task, description: e.target.value });
            }}
          />
        </InputGroup>
        <hr />
        <Row className="mt-3">
          <Col className="text-center">Label Colour:</Col>
          <Col>
            <DropdownButton
              title={
                task.label_color === "#3797a4" ? "default" : task.label_color
              }
              size="sm"
              onSelect={(eventKey) => {
                setTask({ ...task, label_color: eventKey });
              }}
            >
              <Dropdown.Item eventKey="Tomato">Tomato</Dropdown.Item>
              <Dropdown.Item eventKey="MediumSeaGreen">
                MediumSeaGreen
              </Dropdown.Item>
              <Dropdown.Item eventKey="Orange">Orange</Dropdown.Item>
              <Dropdown.Item eventKey="DodgerBlue">DodgerBlue</Dropdown.Item>
            </DropdownButton>
          </Col>
          <Col className="text-center">priority:</Col>
          <Col>
            <DropdownButton
              title={task.priority === "" ? "select..." : task.priority}
              size="sm"
              onSelect={(eventKey) => {
                setTask({
                  ...task,
                  priority: eventKey,
                });
              }}
            >
              <Dropdown.Item eventKey="low">low</Dropdown.Item>
              <Dropdown.Item eventKey="medium">medium</Dropdown.Item>
              <Dropdown.Item eventKey="high">high</Dropdown.Item>
            </DropdownButton>
          </Col>
        </Row>
      </Modal.Body>
      <Modal.Footer>
        <Button
          block
          variant="info"
          onClick={() => props.saveFunction(task)}
          disabled={task.name === "" ? true : false}
        >
          Add Task
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default AddTask;
